.App {
    text-align: center;
    overflow-y: hidden;
    background: #2E3E48;
}

.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
}

.project {
    font-size: 22pt;
    color: white;
    margin-bottom: 10px;
}

.projects-title {
    font-size: 30pt;
    margin-top: 5vh;
    color: white;
}

#project-desc {
    font-size: 16pt;
    color: #B5E56A;
}

#sidebutton {
    position: fixed;
    left: 10px;
    top: 10px;
    z-index: 3;
}

#sidebutton:hover {
    color: cadetblue;
}

#pagetitle {
    margin-top: 5vh;
    font-size: 40pt;
    /*font-family: "Lucida Sans", Monaco, serif;*/
    font-family: monospace, Monaco, Lucida Console;
    color: white;
}

#profile {
    color: white;
    margin-right: 3vw;
}

#profile:hover {
    /*color: cadetblue;*/
    color: #B5E56A
    /*background: #1D2D37;*/
}

.slide {
    display: block;
    margin: auto;
    width: 80%;
    position: center;
}

.wingslide {
    display: block;
    margin: auto;
    width: 60%;
    position: center;
}

#project-img:hover {
    opacity: 0.5;
}

::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.info {
    text-align: left;
    position: absolute;
    left: 10%;
    right: 10%;
    color: white;
}

@media all and (max-width: 980px) {
    .info{
        /*margin-left: 0px;*/
        left: 4%;
        right: 4%;
    }

    #pagetitle {
        text-align: center;
    }

    #profile-wrapper {
        text-align: center;
    }

    #profile {
        margin-left: 1.5vw;
        margin-right: 1.5vw;
    }
}

#infodivider {
    border-top: 1px solid rgba(34, 36, 38, 0.5);
}

#entry {
    color: white;
    margin-bottom: 10px;
    font-size: 20px;
    max-width: 80vw;
}

@media all and (max-width: 980px) {
    #entry{
        max-width: 92vw;
    }
}

#menu {
    margin-top: 35px;
    text-align: center;
}

#menuitem {
    color:white;
    display: inline;
    font-size: 20px;
    padding: 5px;
    margin-left: 7px;
    margin-right: 7px;
}

#menuitem:hover {
    background: #1D2D37;
}

.block {
    font-size: 30px;
    background: #1D2D37;
    font-weight: bold;
    margin-top: 20px;
    width: 100%;
    padding: 4px 0px;
}




